.grid-feature {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: -20px -15px;
  }

  &__item {
    padding: 30px 15px;
    flex: 0 1 100%;
    @include media-breakpoint-up('md') {
      flex: 0 1 50%;
    }
  }

  &__item:hover &__title {
    color: $color-accent;
  }

  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    & > * {
      margin-bottom: 0;
      transition: color $transition;
    }
    // &::before {
    //   content: '<';
    //   background-size: contain;
    //   order: 1;
    //   line-height: 1;
    //   color: $color-accent;
    //   font-size: 16px;
    //   flex: 0 0 auto;
    //   margin-left: 30px;
    // }
    &::after {
      content: '<';
      flex: 1 1 auto;
      height: 1px;
      order: 1;
      background: $color-accent;
      margin-left: 30px;
      margin-right: 10px;
      color: $color-accent;
      line-height: 1px;
      font-size: 16px;
      min-width: 30px;
    }
  }

  &__text {

  }
}