$transition-work-main: 0.5s; 

.grid-works {
  &__nav {
    display: flex;
    justify-content: flex-start;
    padding: 15px 15px;
    @include media-breakpoint-up('sm') {
      justify-content: center;
    }
    &__wrapper {
      display: flex;
      align-items: flex-start;
      margin: 0 calc(-10px - 3.5vw);
      flex: 0 1 auto;
      flex-direction: column;
      max-width: calc(524px + 7vw);
      @include media-breakpoint-up('sm') {
        flex-wrap: wrap;
        display: inline-flex;
        flex-direction: row;
        align-items: center;
      }
      @media (min-width: 790px) {
        max-width: none;
      }
    }

    &__item {
      white-space: nowrap;
      padding: 12px 10px;
      text-transform: uppercase;
      transition: color $transition;
      margin: 5px 3.5vw;
      &:hover, &.active {
        color: $color-accent;
      }
    }
  }

  &__more {
    &__btn {
      width: 100%;
    }
  }

  &__wrapper, &__slide {
    display: flex;
    flex-wrap: wrap;
  }

  &:not(.loaded-slider) &__slide:not(:first-child) {
    display: none;
  }

  &.loaded-slider &__slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    transition: opacity 0.4s;
    pointer-events: none;
    &.active {
      opacity: 1;
      pointer-events: all;
      transition: opacity 0.4s 0.2s;
    }
  }

  &__wrapper { // min-width img - 544px
    max-width: 514px;
    margin-left: auto;
    margin-right: auto;
    transition: height 0.5s;
    position: relative;
    @media (min-width: 790px) {
      max-width: none;
    }
    @media (min-width: 992px) {
      max-width: 920px;
    }
    @media (min-width: 1200px) {
      max-width: none;
    }
  }

  &__wrapper.filtered{

  }
  &__wrapper.filtered &__item {
    opacity: 0 !important;
    transition: opacity 0.4s, transform 0s 0.5s, left 0s 0.5s, top 0s 0.5s !important;
   
  }

  &__item {
    display: flex;
    width: 100%;
    align-items: flex-end;
    color: $color-text;
    position: relative;
    overflow: hidden;
    will-change: transform;
    transition: opacity 0.4s, transform 0s 0s, left 0s 0s, top 0s 0s !important;
    @media (min-width: 790px) {
      width: 50%;
    }
    @media (min-width: 1200px) {
      width: 33.333%;
    }
    @media (min-width: 1600px) {
      width: 25%;
    } 
    @media (min-width: 1920px) {
      width: 400px;
      flex: 1 1 auto;
      max-width: 25%;
    }
    &:hover {
      color: $color-text;
      &::after {
        opacity: 1;
      }
    }
    &::before {
      content: '';
      padding-top: 56.25%;
      width: 0;
      display: block;
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.35);
      opacity: 0;
      transition: opacity $transition-speed;
    }
  }

  &__slide--big {
    @media (min-width: 1600px) {
      justify-content: center;
    }
  }

  &__slide--big &__item {
    @media (min-width: 1600px) {
      width: 33.333%;
      max-width: none;
    }
    @media (min-width: 1920px) {
      width: 585px;
      max-width: 33.333%;
    }
  }

  &__item:hover &__inner {
    transform: translate(0, 0);
    opacity: 1;
  }

  &__slide &__item {
    &:nth-child(n + 7) {
      display: none;
      @media (min-width: 1920px) {
        display: flex;
      }
    }
    @media (min-width: 1600px)  {
      &:nth-child(n - 9) {
        display: flex;
      }
    }
  }

  &__inner {
    padding: (20rem/16) (25rem/16);
    position: relative;
    z-index: 1;
    transform: translate(0, 50%);
    opacity: 0;
    transition: transform $transition-speed, opacity $transition-speed;
    .h3 {
      margin: 0;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
  }

  &__tag {
    white-space: nowrap;
    text-transform: lowercase;
    &::after {
      content: '/';
      margin-left: 0.3em;
      margin-right: 0.3em;
    }
  }
  &__tag:last-child {
    &::after {
      content: '';
      display: none;
    }
  }
}

.touch-browser .grid-works__item::after {
  opacity: 1;
}
.touch-browser .grid-works__inner {
  transform: translate(0, 0);
  opacity: 1;
}