.form-two-col {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: (-15rem/16);
  margin-right: (-15rem/16);
  margin-bottom: (-30rem/16);
  & > * {
    flex: 0 1 calc(100% - #{30rem/16});
    margin: 0 (15rem/16) (30rem/16);
    @include media-breakpoint-up('sm') {
      flex: 0 1 calc(50% - #{30rem/16});
    }
  }
  .form-two-col__full {
    flex: 0 1 calc(100% - #{30rem/16});
  }
}