.footer {
  align-self: flex-end;
  margin-top: auto;
  padding-top: 70px;
  padding-bottom: 40px;
  display: flex;
  text-align: center;

  &__wrapper {
    margin: -15px;
    flex-direction: column;
    text-align: center;
    display: inline-flex;
    width: auto;
    @include media-breakpoint-up('sm') {
      flex-direction: row;
      flex-wrap: wrap;
      text-align: left;
      align-items: flex-start;
      display: flex;
      width: 100%;
    }
    @include media-breakpoint-up('xl') {
      flex-wrap: nowrap;
    }
  }

  &__item {
    padding: 15px;
  }

  &__title {
    @include media-breakpoint-up('sm') {
      margin-right: auto;
    }
  }

  &__info {
    flex-grow: 1;
    order: 1;
    width: 100%;
    @include media-breakpoint-up('xl')  {
      order: 0;
      width: auto;
    }
    .info-company__wrapper {
      justify-content: space-between;
      @include media-breakpoint-up('xl') {
        justify-content: center;
      }
    }
  }

  &__links {
    .socials-links {
      width: 100%;
      justify-content: center;
    }
    @include media-breakpoint-up('sm') {
      margin-left: auto;
      .socials-links {
        width: auto;
        justify-content: flex-end;
      }
    }
  }
}