.btn {
  display: inline-block;
  line-height: (20/16);
  font-weight: 400;
  cursor: pointer;
  padding: (18em/16) (30em/16);
  text-align: center;
  min-width: (100em/16);
}

.btn--full {
  color: $color-text;
  background: $color-accent;
  transition: box-shadow $transition;

  &:hover {
    color: $color-text; 
    box-shadow: inset 0px 4px 4px rgba(0, 63, 109, 0.6);
  }
}

.btn--black {
  color: $color-text;
  background: $color-background;
  transition: background-color $transition, color $transition;
  &:hover {
    color: $color-text;
    background: $color-accent;
  }
}

.btn--line {
  color: $color-text;
  background-color: transparent;
  box-shadow: inset 0 0 0 1px #fff;
  transition: box-shadow $transition, background-color $transition;
  &:hover {
    color: $color-text; 
    box-shadow: inset 0 0 0 1px transparent;
    background: $color-accent;
  }
}

.btn--big-min-width {
  min-width: (250em/16);
  @media (min-width: 330px) {
    min-width: (270em/16);
  }
}
.btn--one-line {
  padding: 1em;
  box-shadow: inset 0 -1px 0 rgba(#fff, 0.5);
  transition: color $transition, box-shadow $transition;
  &:hover {
    color: $color-accent;
    box-shadow: inset 0 -1px 0 rgba($color-accent, 0.5);
  }
}

.btn-icon {
  display: inline-block;
  cursor: pointer;
  text-align: center;
  background: none;
  border-radius: 50%;
  position: relative;
  width: 42px;
  &::before {
    content: '';
    display: block;
    padding-top: 100%;
    width: 0;
  }

  img, svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: calc(100% - 16px);
    max-height: calc(100% - 20px);
  }
}

.btn-icon--phone {
  background-color: $color-accent;
  transition: background-color $transition;
  &:hover {
    background-color: rgba($color-accent, 0.7);
  }
}