.no-margin-b {
  margin-bottom: 0 !important;
}


//mod row
.margin-tb-40.row {
  margin-top: -1rem;
  margin-bottom: -1rem;
  @include media-breakpoint-up('sm') {
    margin-top: -1.5rem;
    margin-bottom: -1.5rem;
  }
  @include media-breakpoint-up('md') {
    margin-top: -2rem;
    margin-bottom: -2rem;
  }
  @include media-breakpoint-up('lg') {
    margin-top: -2.5rem;
    margin-bottom: -2.5rem;
  }
  & > * {
    padding-top: 1rem;
    padding-bottom: 1rem;
    @include media-breakpoint-up('sm') {
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
    }
    @include media-breakpoint-up('md') {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
    @include media-breakpoint-up('lg') {
      padding-top: 2.5rem;
      padding-bottom: 2.5rem;
    }
  }
}