.label {
  display: block;
  // margin-top: (60em/20);
  padding-bottom: 1.3em;
}

.input {
  display: block;
  width: 100%;
  color: $color-text;
  border: 1px solid #666666;
  padding: 0 (18em/16);
  font-size: inherit;
  position: relative;
  min-height: (56em/16);

  input, textarea {
    color: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
    z-index: 1;
  }
  input {
    padding: 0 (18em/16);
  }
  textarea {
    padding: (12em/16) (18em/16);
  }
}
input.input {
  height: (56em/16);
}
.input.invalid-input {
  border-color: #F31D1D;
}
textarea.input {
  padding-top: (12em/16);
  padding-bottom: (12em/16);
  height: (56em/16);
}

textarea.input, .input textarea {
  resize: none;
}

.input + .label {
  margin-top: 2em;
}

.textarea--auto-height {
  padding-top: (12em/16);
  padding-bottom: (12em/16);
  &__text {
    opacity: 0;
    z-index: -1;
    white-space: pre-wrap;
  }
  textarea {
    overflow: hidden;
  }
}

.radio-input {
  &__label {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    align-items: center;
    margin-bottom: (30rem/16);
    cursor: pointer;
    &:last-child {
      // margin-bottom: 0;
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      opacity: 0;
    }

    &--line {
      display: inline-flex;
      margin-right: (48rem/16);
      margin-bottom: 0.5em;
      &:last-child {
        margin-right: 0;
      }
    }
    &--line-half {
      display: inline-flex;
      width: 50%;
      &:nth-child(2n) {
        padding-right: 14px;
      }
      &:nth-child(2n + 1) {
        padding-left: 14px;
      }
    }
  }

  &__check {
    min-width: 16px;
    min-height: 16px;
    width: 1em;
    height: 1em;
    border: 1px solid #FFFFFF;
    margin-right: 0.5em;
    border-radius: 50%;
    order: -1;
    position: relative;
    display: inline-flex;
    padding: 4px;
    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      // background: $color-accent;
    }
  }

  &:checked ~ &__check, &:checked + &__check {
    &:before {
      background: $color-accent;
    }
  }

  &__content {
    margin-top: 1rem;
    flex: 1 1 100%;
    margin-bottom: 1rem;
  }
  span {
    flex: 1 1 0%;
  }
}

.checkbox-input__label.invalid-input .checkbox-input__check, .radio-input__label.invalid-input .radio-input__check {
  border-color: #F31D1D;
}

.checkbox-input {
  &__label {
    display: flex;
    position: relative;
    align-items: center;
    margin-bottom: (30rem/16);
    cursor: pointer;
    &:last-child {
      // margin-bottom: 0;
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      opacity: 0;
    }

    &--line {
      display: inline-flex;
      margin-right: (48rem/16);
      margin-bottom: 0.5em;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__check {
    min-width: 16px;
    min-height: 16px;
    width: 1em;
    height: 1em;
    border: 1px solid #FFFFFF;
    margin-right: 0.5em;
    position: relative;
    order: -1;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: url('../img/check.svg') no-repeat center center $color-accent;
      background-size: contain;
      opacity: 0;
    }
  }

  &:checked ~ &__check, &:checked + &__check {
    border-color: $color-accent;
    &::before {
      opacity: 1;
    }
  }
}