.header {
  padding-top: 20px;
  padding-bottom: 20px;
  align-self: flex-start;
  margin-bottom: auto;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__button {
    flex: 0 1 auto;
    position: relative;
    padding-right: 25px;
    margin-right: 15px;
    z-index: 100;
  }
  &__burger {
    position: absolute;
    width: 18px;
    height: 12px;
    top: 50%;
    margin-top: -6px;
    right: 0;
    span {
      display: block;
      height: 1px;
      position: absolute;
      width: 100%;
      top: 5px;
      left: 0;
      background-color: $color-text;
      transition: transform $transition-speed, opacity $transition-speed, background-color $transition-speed;
    }
    span:first-child {
      transform-origin: 0% 50%;
      top: 0;
    }
    span:last-child {
      top: auto;
      bottom: 0;
      transform-origin: 0% 50%;
      transform: scaleX(0.6);
    }    
  }

  &__button.active &__burger {
    span {
      transform: scaleX(0);
      opacity: 0;
      background-color: $color-accent;
    }
    span:first-child {
      transform: rotate(45deg) translate(1px, -2px);
      opacity: 1;
    }
    span:last-child {
      transform: rotate(-45deg) scaleX(1) translate(1px, 2px);
      opacity: 1;
    }
  }

  &__call {
    flex: 0 0 auto;
    opacity: 1;
    transition: opacity $transition-speed, background-color $transition-speed;
  }

  &__menu {
    background: $color-background;
    position: fixed;
    z-index: 99;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: 100vw;
    max-height: 100vh;
    opacity: 0;
    transform: translate(-100px, 0px) scale(1);
    pointer-events: none;
    transition: opacity $transition-speed, transform $transition;

    &__outer {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      max-width: 100%;
      max-height: 100%;

      top: 70px;
      max-height: calc(100% - 70px);
      @include media-breakpoint-up('md') {
        top: 0px;
        max-height: 100%;
      }
      
      display: flex;
      overflow-y: auto;
      overflow-x: hidden;
    }

    &__wrapper {
      padding: 0px 0px 50px;
      margin: auto;
      max-width: 100%;
      @include media-breakpoint-up('md') {
        padding: 70px 0px 50px;
      }
      & > * {
        margin-bottom: 20px;
        @include media-breakpoint-up('md') {
          margin-bottom: 30px;
        }
        @include media-breakpoint-up('lg') {
          margin-bottom: 40px;
        }
        @include media-breakpoint-up('xl') {
          margin-bottom: 50px;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &__form {
      &__wrapper {
        margin: auto;
        padding: 0px 15px 50px;
        display: flex;
        justify-content: center;
        @include media-breakpoint-up('md') {
          padding: 70px 15px 50px;
        }
      }
    }

    &__list {
      list-style: none;
      padding-left: 0px;
      margin-left: -25px;
      margin-right: -15px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      ul {
        list-style: none;
        padding-left: 0;
        margin: 0;
      }
      & > li > a, & > li > span {
        @include all-h2; 
        cursor: pointer;
        transition: color $transition;
        position: relative;
        z-index: 4;
        padding: 5px 0 5px 25px;
        &::before {
          content: '';
          width: 10px;
          height: 11px;
          background: url('../img/menu-mark.svg') no-repeat center center;
          background-size: contain;
          position: absolute;
          left: 10px;
          top: 50%;
          transform: translate(-100%, -50%);
          opacity: 0;
          transition: opacity $transition, transform $transition;
        }
      }
      & > li {
        display: inline-flex;
        position: relative;
        align-items: center;
        padding-right: 15px;
      }
    }
    &__sublist {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      position: absolute;
      z-index: 3;
      top: 50%;
      left: 100%;
      opacity: 0;
      transform: translate(0, -50%);
      transition: opacity $transition;
      margin: 0 (-5em/16) 0 calc(#{-5em/16} - 1px);
      width: 100vw;
      min-height: 100%;
      pointer-events: none;
      a {
        padding: (5em/16);
        margin: 0 (5em/16);
        color: $color-text-gray;
        transition: color $transition;
        white-space: nowrap;
        &:hover {
          color: $color-text;
        }
      }
    }
  }
}

.header.active-menu {
  .header__menu {
    opacity: 1;
    transform: translate(0, 0) scale(1);
    pointer-events: all;
  } 
  .header__wrapper .header__call {
    opacity: 0;
  }
}

.touch-browser .header__menu__list {
  & > li {
    align-items: flex-start;
    &.active {
      & > a, & > span {
        color: $color-accent;
        &::before {
          opacity: 1;
          transform: translate(0%,-50%);
        }
      }
      .header__menu__sublist {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
} 
.courser-browser .header__menu__list {
  & > li {
    &:hover {
      & > a, & > span {
        color: $color-accent;
        &::before {
          opacity: 1;
          transform: translate(0%,-50%);
        }
      }
      .header__menu__sublist {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
}
.touch-browser .header__menu__sublist {
  bottom: 0;
  top: auto;
  transform: translate(0, 0%);
  left: 15px;
  min-height: auto;
}


.header__menu__outer:not(.header__menu__outer--lid) {
  .header__menu__form__wrapper {
    display: none;
  }
}
.header__menu__outer.header__menu__outer--lid {
  top: 70px;
  max-height: calc(100% - 70px);
  @include media-breakpoint-up('lg') {
    top: 0px;
    max-height: 100%;
  }
  .header__menu__wrapper {
    display: none;
  }
}