html {
  height: 100%;
  overflow-x: hidden;
}
body {
  color: $color-text;
  background: $color-background;
  position: relative;
  overflow: hidden;
}

body {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 100vh;
  & > * {
    flex: 1 1 100%;
  }
}

.scroll-hidden {
  overflow-y: hidden;
}