.slides-form {
  opacity: 0;
  pointer-events: none;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  .h2 {
    text-align: center;
  }
  &.loaded {
    opacity: 1;
    pointer-events: all;
  }
  &__inner {
    overflow: hidden;
    display: flex;
  }

  &__outer {
    display: flex;
    align-items: flex-start;
    flex: 0 0 auto;
    max-width: 100%;
  }
  &.loaded &__outer {
    // transition: transform 0.4s ease-in-out;
  }

  &__slide {
    // width: 100%;
    flex: 0 0 auto;
    max-width: 100%;
    opacity: 0;
    transition: opacity 0.5s;
    &:first-child {
      align-self: center;
    }
    &.active {
      opacity: 1;
    }
  }

  &__proggress {
    text-align: center;
    margin-bottom: (30rem/16);
    &__line {
      height: 1px;
      background: $color-gray;
      width: 100%;
      max-width: 570px;
      position: relative;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: (22rem/16);
    }

    &__bar {
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translate(0px, 0px);
      transition: transform 0.2s;
      background: $color-accent;
      width: 25%;
    }
  }

  &__btns {
    display: flex;
    margin-left: -10px;
    margin-right: -10px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap-reverse;
    .btn {
      margin: 5px 10px 5px;
    }
  }

  .input + &__btns {
    padding-top: 30px;
  }
}

.touch-browser .slides-form.loaded {
  .slides-form__outer {
    // transition: transform 0s;
  }
  .slides-form__slide {
    // opacity: 0;
    // transition: opacity 0.5s;
    // &.active {
    //   opacity: 1;
    // }
  }
}