.section-1 {
  &__logo {
    position: absolute; top:50%; right: 0; max-width: 60%;
    img {
      margin-top: -50%;
    }
  }

  &__phone {
    display: inline-block;
    img {
      margin-top: 2.5rem; 
      margin-bottom: -25%;
      @include media-breakpoint-up('lg') {
        margin-top:-22%; 
      }
    }
  }
}