.info-company {
  &__wrapper {
    margin: -15px -20px;
    display: inline-flex;
    flex-wrap: wrap;
    flex-direction: column;
    @include media-breakpoint-up('sm') {
      display: flex;
      flex-direction: row;
    }
  }
  &__item {
    padding: 15px 20px;
    display: flex;
    align-items: flex-start;
    @include media-breakpoint-up('sm') {
      max-width: 50%;
    }
    @include media-breakpoint-up('md') {
      max-width: 33.333%;
    }
    img, svg {
      width: 1em;
      margin-right: 0.5em;
      margin-top: 0.25em;
    }
  }

  &__text {
    position: relative;
    text-align: left;
  }

  &__time {
    &__work, &__not-work {
      position: relative;
      padding-left: 12px;
      &:before {
        content: '';
        position: absolute;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        top: 50%;
        left: 0;
        margin-top: -3px;
      }
    }

    &__work {
      &::before {
        background: #1DF34C;
      }
    }

    &__not-work {
      display: none;
      &::before {
        background: #F31D1D;
      }
    }

    &--not-work &__work {
      display: none;
    }

    &--not-work &__not-work {
      display: block;
    }
  }
}

.info-company.info-company--col{
  .info-company__wrapper {
    flex-direction: column;
    align-items: stretch;
  }
  .info-company__item {
    max-width: none;
  }
}